<template>
  <div>
    <Layout-box one="热门兑换商品">
      <el-button @click="onAdd">添加商品</el-button>
      <table-cmp :tableData="tableData" :tableColumn="tableColumn" @delete="onDelete" @edit="onEdit" />
      <el-pagination class="mt-1 text-right" background :current-page="queryInit.pageNo" :page-sizes="[10, 20, 30, 40]" :page-size="queryInit.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="queryInit.total" @size-change="handleSizeChange" @current-change="handleCurrentChange" />
    </Layout-box>

    <article>
      <hot-goods-add :dialogVisible="dialogVisible" @close="onClose" />
      <hot-goods-edit :dialogVisible="editVisible" :selectData="selectData" @close="onCloseEdit" />
    </article>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox'
import tableCmp from '@/components/table_cmp/index.vue'
import hotGoodsAdd from './cmp/hot_goods_add.vue'
import hotGoodsEdit from './cmp/hot_goods_edit.vue'

export default {
  name: 'addHotGoods',

  data () {
    return {
      queryInit: {
        pageNo: 1,
        pageSize: 10,
        total: 0,
      },
      tableData: [], // 规则列表
      tableColumn: [
        { label: '名称', prop: 'name', type: 'text' },
        { label: '积分', prop: 'wohoCoin', type: 'text' },
        { label: '操作', type: 'button', btnGroup: [
          { value: 'delete', name: '删除' },
          { value: 'edit', name: '修改' }
        ] }
      ],

      // -------------------------------
      dialogVisible: false,
      editVisible: false,
      selectData: {},
    }
  },
  
  components: {
    LayoutBox,
    tableCmp,
    hotGoodsAdd,
    hotGoodsEdit,
  },

  async created () {
    await this.apiForGoods()
  },

  methods: {
    handleSizeChange (pageSize) {
      this.queryInit.pageSize = pageSize
    },
    handleCurrentChange (pageNo) {
      this.queryInit.pageNo = pageNo
    },
    // 点击新增规则
    onAdd () {
      this.dialogVisible = true
    },
    // 关闭添加商品窗口
    onClose () {
      this.dialogVisible = false
    },
    onCloseEdit (scope) {
      this.editVisible = false
    },
    // 点击删除
    async onDelete (e) {
      const data = {
        prodId: e.row.id,
        type: 1
      }
      const res = await this.$http.post(this.$api.integralSignInUpdate, data)
      if (res.data.success) {
        this.$message.success('删除成功')
        await this.apiForGoods()
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // 点击修改
    onEdit (scope) {
      this.selectData = scope.row
      this.editVisible = true
    },

    // ------------------------------------------
    // 查询已添加的热门兑换商品
    async apiForGoods () {
      const data = {
        ...this.queryInit
      }
      const res = await this.$http.post(this.$api.findIntegralProd, data)
      this.tableData = res.data.data.list
      this.queryInit = res.data.data
    }
  }
}
</script>

