<template>
  <el-dialog
    title="修改商品"
    :close-on-click-modal="false"
    :visible.sync="dialogVisible"
    width="30%"
    :before-close="handleClose"
  >
    <el-form :model="form">
      <el-form-item label="商品：">
        <span>{{ selectData.name }}</span>
      </el-form-item>
      <el-form-item label="蜗币兑换数量：">
        <el-input v-model.number="form.wohoCoin"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="handleClose">取 消</el-button>
      <el-button type="primary" @click="onSave">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: 'hotGoodsEdit',

  props: {
    dialogVisible: {
      type: Boolean,
      default: false,
    },
    selectData: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },

  data () {
    return {
      tableData: [],
      form: {
        prodId: '',
        wohoCoin: '',
      }
    }
  },

  watch: {
    selectData (newV, oldV) {
      this.form = {
        prodId: newV.id,
        wohoCoin: newV.wohoCoin
      }
    }
  },

  async created () {
    await this.apiForGoods()
    this.form = {
      prodId: this.selectData.id,
      wohoCoin: this.selectData.wohoCoin
    }
  },
  
  methods: {
    handleClose () {
      this.$emit('close')
    },
    async onSave () {
      const data = {
        ...this.form
      }
      const res = await this.$http.post(this.$api.integralSignInUpdate, data)
      if (res.data.success) {
        this.$message.success('保存成功')
        this.handleClose()
        this.reload()
      } else {
        this.$message.error(res.data.msg)
      }
    },

    // ------------------------------------------
    // 获取单个规格的商品用来添加到热门兑换商品里面
    async apiForGoods () {
      const data = {
        pageSize: 9999,
      }
      const res = await this.$http.post(this.$api.forGoods, data)
      this.tableData = res.data.data.list
    }
  },
  
  inject: ['reload']
}
</script>

